'use client'
import React, { useState } from 'react';
import Link from 'next/link';
import { useRouter } from 'next/navigation';
import { 
  Home, 
  Package, 
  Tags, 
  ShoppingCart, 
  BarChart2, 
  FileText, 
  DollarSign,
  Menu as MenuIcon,
  X
} from 'lucide-react';


export default function page() {
    const [sidebarOpen, setSidebarOpen] = useState(true);
    const router = useRouter();
  
    const toggleSidebar = () => {
      setSidebarOpen(!sidebarOpen);
    };
  
    const menuItems = [
      { path: '/admin', icon: <Home size={20} />, title: 'Dashboard' },
      { path: '/admin/categories', icon: <Tags size={20} />, title: 'Category Management' },
      { path: '/admin/products', icon: <Package size={20} />, title: 'Product Management' },
      { path: '/admin/inventory', icon: <FileText size={20} />, title: 'Inventory Management' },
      { path: '/admin/sales', icon: <BarChart2 size={20} />, title: 'Sales Tracking' },
      { path: '/admin/orders', icon: <ShoppingCart size={20} />, title: 'Order Management' },
      { path: '/admin/insights', icon: <DollarSign size={20} />, title: 'Profit Insights' },
    ];
  
  

  return (
    <>

<div className={`sidebar bg-dark text-light ${sidebarOpen ? 'open' : 'closed'}`} style={{
      width: sidebarOpen ? '250px' : '70px',
      minHeight: '100vh',
      transition: 'width 0.3s ease',
      position: 'fixed',
      left: 0,
      top: 0,
      zIndex: 1000
    }}>
      <div className="d-flex justify-content-between align-items-center p-3 border-bottom border-secondary">
        {sidebarOpen && <h4 className="mb-0">Admin Panel</h4>}
        <button className="btn btn-dark" onClick={toggleSidebar}>
          {sidebarOpen ? <X size={20} /> : <MenuIcon size={20} />}
        </button>
      </div>
      
      <ul className="nav flex-column mt-3">
        {menuItems.map((item, index) => (
          <li key={index} className="nav-item">
                        <Link 
              href={item.path}
              className={`nav-link d-flex align-items-center p-3 ${router.pathname === item.path ? 'active bg-primary text-white' : 'text-light'}`}
            >
              <span className="me-3">{item.icon}</span>
              {sidebarOpen && <span>{item.title}</span>}
            </Link>

            
          </li>
        ))}
      </ul>
    </div>

    
    
    </>
  )
}
